import React, { Suspense, lazy } from "react";
const MediaIcon = lazy(() => import('../../Elements/MediaIcon/MediaIcon'));
const Image = lazy(() => import('../../Elements/Media/Image'));
const Video = lazy(() => import('../../Elements/Media/Video'));
const Network = lazy(() => import("../../Elements/Network/Network"));
const Media = ({ itemData, wallID, ThemeID, ownerId, wall }) => {
  const isTagembed = wall?.User?.userDatabaseType == "tagembed"
  const mediaIcon = itemData.hotspot ? false : true
  const isVideo = (itemData.type === 3 || itemData.type === 5) && (wall.ThemeRule.autoPlay == 1) ? true : false
  return <div className="tb_sp_media_wrap" role="button" tabIndex="0" aria-label="Pop Up Button">
    <Suspense fallback={<></>}>
    {isTagembed ? <div className="tb_sp_social_top_">
      <Network networkClass={'tb_sp_social__ico'} network={itemData.network} isDefault={wall.ThemeRule.iconType === 1 ? true : false} ThemeRule={wall.ThemeRule} />
    </div> : null}
    <MediaIcon itemData={itemData} IconClass={`tb_sp_media_icon${isTagembed ? ' tb_sp_media_icon_te' : ''}`} show={mediaIcon} isCenter={isTagembed} />
    {isVideo ? <Video VideoClass="tb_sp_video" data={itemData} wallID={wallID} themeID={wall.Personalization.widgetTheme} ownerId={ownerId} size={100} isCover={true} muted={true} autoPlay={true} />
      : <Image ImageClass="tb_sp_image" data={itemData} wallID={wallID} themeID={ThemeID} ownerId={ownerId} size={100} hotspot={itemData.hotspot ? true : false} />}
      </Suspense>
  </div>
}
export default Media